.item {
  background-color: #fff;
  width: 276px;

  box-shadow: 0 0 6px 0 #adacac;
  display: flex;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;

  margin: 10px 0;
}

.status {
  margin-left: 10px;
  width: 20px;
  height: 20px;
  border-radius: 100%;
}

.active {
  composes: status;
  background-color: green;
}
.noActive {
  composes: status;
  background-color: red;
}

.avatar {
  height: 80px;
  width: 80px;
  background: gray;
  border-radius: 10px;
  margin: 0 14px;
}

.name {
  font-weight: bold;
  font-size: 22px;
}

ul {
  list-style-type: none;
  padding: 0;
}
